.features {
	&_fun {
		padding: 90px 0 53px;
		@include lg {
			padding: 60px 0 40px; }
		@include md {
			padding: 45px 0 40px;
		 @include xs {
			 padding: 40px 0 30px; } } }
	&_fun &__row {
		@include xs {
			text-align: center; } }
	&_fun &__item {
		width: 20%;
		@include xs {
			width: 33.33333%;
			min-width: 140px;
			padding-bottom: 15px; } }
	&_fun &__item-pic {
		margin-bottom: 9px; }
	&_rest {
		padding: 55px 0 67px;
		@include md {
			padding: 40px 0; }
		@include xs {
			padding: 20px 0 30px; } }
	&_rest &__item {
		@include xs {
			width: 50%;
			padding: 0 5px; } }
	&_restaurant {
		padding: 80px 0 30px;
		@include lg {
			padding: 40px 0 30px; }
		@include md {
			padding: 25px 0 10px; }
		@include sm {
			padding: 10px 0; } }
	&_restaurant &__container {
		max-width: 1280px; }
	&_restaurant &__row {
		margin: 0 -15px;
		display: flex;
		justify-content: space-between;
		@include sm {
			margin: 0 -5px;
			flex-wrap: wrap;
			justify-content: space-around; } }
	&_restaurant &__item {
		width: 20%;
		padding: 0 10px;
		@include md {
			padding: 0 3px; }
		@include sm {
			width: 33.33333% !important; }
		@include xs {
			width: 50% !important; }
		@include r(479) {
			width: 100% !important; } }
	&_restaurant &__item:nth-child(1) {
		width: 14%;
		@include sm {
			order: 0; } }
	&_restaurant &__item:nth-child(1) &__item-descr {
		max-width: 160px; }
	&_restaurant &__item:nth-child(2) {
		width: 23.2%;
		@include sm {
			order: 1; } }
	&_restaurant &__item:nth-child(2) &__item-descr {
		max-width: 220px; }
	&_restaurant &__item:nth-child(3) {
		width: 23%; }
	&_restaurant &__item:nth-child(3) &__item-descr {
		max-width: 274px; }
	&_restaurant &__item:nth-child(4) {
		width: 19.5%; }
	&_restaurant &__item:nth-child(4) &__item-descr {
		max-width: 228px; }
	&_restaurant &__item:nth-child(5) {
		width: 18.6%; }
	&_restaurant &__item:nth-child(5) &__item-descr {
		max-width: 220px; }
	&_restaurant &__item-descr {
		max-width: none;
		line-height: 1.1; }
	&_restaurant span {
		font-size: 16px; }
	&__row {
		font-size: 0; }
	&__item {
		width: 25%;
		display: inline-block;
		vertical-align: top;
		padding: 0 15px;
		text-align: center;
		@include sm {
			padding: 0 10px; }
		&-pic {
			height: 100px;
			margin-bottom: 12px;
			@include vertical;
			@include sm {
				transform: scale(.8);
				margin-bottom: 0; }
			&::after {
				display: inline-block;
				vertical-align: middle;
				margin: 0 auto;
				content: ''; } }
		&-pic_modern::after {
			@include s('modern'); }
		&-pic_refrigerator::after {
			@include s('refrigerator'); }
		&-pic_repair::after {
			@include s('repair'); }
		&-pic_ship::after {
			@include s('ship'); }
		&-pic_menu::after {
			@include s('menu'); }
		&-pic_restaurant::after {
			@include s('restaurant'); }
		&-pic_diet::after {
			@include s('diet'); }
		&-pic_national::after {
			@include s('national'); }
		&-pic_ice-cream::after {
			@include s('ice-cream'); }
		&-pic_disco::after {
			@include s('disco'); }
		&-pic_conference::after {
			@include s('conference'); }
		&-pic_horse::after {
			@include s('horse'); }
		&-pic_masterclass::after {
			@include s('masterclass'); }
		&-pic_nutrition::after {
			@include s('nutrition'); }
		&-descr {
			max-width: 215px;
			margin: 0 auto;
			font-size: 18px;
			line-height: 1.3;
			@include lg {
				font-size: 17px; }
			@include sm {
				font-size: 15px; }
			strong {
				display: block;
				@include f('cb'); } } } }
