.header {
	&__top-line {
		padding: 13px 0;
		background-color: #22273b;
		@include sm {
			padding: 10px 0 6px; }
		@include xs {
			padding: 5px 0; } }
	&__container {
		@include clr; }
	&__screen-container {
		@include clr;
		@include xs {
			display: flex;
			flex-direction: column;
			&::after {
				content: none; } } }
	&__nav {
		float: left; }
	&__location {
		float: right;
		@include xs {
			margin-top: 9px; }
		.dropdown__list {
			top: 141%;
			@include sm {
				top: 125%; } }
		.dropdown__text {
			@include sm {
				margin-left: 15px;
				font-size: 14px; }
			@include xs {
				margin-left: 18px;
				font-size: 16px; } } }
	&__screen {
		position: relative;
		min-height: 430px;
		padding: 50px 0 40px;
		height: calc(100vh - 222px);
		@include sm {
			padding: 40px 0 60px;
			height: auto;
			min-height: 0; }
		@include xs {
			padding: 35px 0 45px; }
		&-picture {
			@include cover;
			@include coverdiv;
			z-index: -1;
			@include lg {
				background-position: 0 60%; }
			@include sm {
				background-size: 200%;
				background-position: 28% 70%; }
			@include xs {
				background-size: cover;
				background-position: 40% 50%; } }

		&-title {
			padding-top: 95px;
			@include f('cb', 41);
			color: $white;
			text-shadow: 0 1px 2px rgba(0, 2, 1, 0.8);
			text-transform: uppercase;
			text-align: center;
			line-height: 1.3;
			@include lg {
				padding-top: 110px;
				font-size: 37px; }
			@include md {
				font-size: 35px; }
			@include sm {
				padding-top: 55px;
				font-size: 28px; }
			@include xs {
				font-size: 25px; }
			@include r(399) {
				font-size: 18px; }

			div:nth-child(3) {
				margin-top: 45px;
				@include lg {
					margin-top: 15px; }
				@include sm {
					margin-top: 10px; } } } }

	&__logo {
		float: left;
		@include xs {
			float: none;
			order: 1;
			align-self: flex-start; } }
	&__menu {
		float: left;
		margin: 11px 0 0 30px;
		@include xs {
			order: 3; } }
	&__call-me {
		float: left;
		margin-left: 30px;
		@include lg;
		padding-left: 65px;
		@include md {
			padding-left: 0;
			margin-left: 12px; }
		@include sm {
			text-align: right;
			margin-left: 44px;
			margin-top: -12px; }
		@include xs {
			margin: -60px 0 0;
			align-self: flex-end;
			order: 2; } }
	&__pick-btn {
		float: right;
		@include lg {
			padding: 15px 31px; }
		@include md {
			margin-top: 3px;
			padding: 10px 17px;
			font-size: 16px; }
		@include sm {
			margin-top: 10px;
			padding: 7px 21px;
			font-size: 15px; }
		@include xs {
			float: none;
			order: 2;
			align-self: flex-end; } } }


/* nav
	----------------------------------- */
.nav {
	@include xs {
		display: flex;
		justify-content: center;
		align-items: center;
		position: fixed;
		min-width: 320px;
		top: 51px;
		right: 0;
		left: 0;
		bottom: 0;
		padding: 0;
		z-index: 10;
		background-color: #22273b;
		opacity: 0;
		pointer-events: none;
		transform: translateX(-100%);
		transition: opacity .5s, transform .5s;
		overflow: auto;
		&.is-active {
			opacity: 1;
			pointer-events: all;
			transform: translateX(0);
			.nav__list-item {
				transform: translateX(0);
				opacity: 1; }
			.nav__list-item::after {
				transform: translateX(0); } } }
	&__list {
		font-size: 0;
		@include xs {
			display: flex;
			justify-content: space-between;
			flex-basis: 100%;
			align-items: stretch;
			flex-direction: column;
			overflow: auto;
			padding: 0;
			text-align: center; }
		&-item {
			display: inline-block;
			vertical-align: top;
			&.is-active .nav__link {
				color: $blue; }
			& + & {
				margin-left: 40px;
				@include md {
					margin-left: 25px; }
				@include sm {
					margin-left: 11px; }
				@include xs {
					margin-left: 0; } }
			@include xs {
				position: relative;
				transition: transform .3s ease-in, opacity .3s;
				opacity: 0;
				transform: translateX(-100%);
				transition-delay: .2s;
				&:not(:last-child) {
					margin-bottom: 20px; }
				&::after {
					transform: translateX(-200%);
					width: 100%;
					content: '';
					border-bottom: 1px solid rgba($blue, .5);
					position: absolute;
					left: 0;
					top: 50%;
					z-index: 2;
					transition-delay: 3.5s;
					transition: transform 1.5s ease-in;
					pointer-events: none; } } }
		&-link {
			font-size: 16px;
			color: $white;
			@include tr(color .15s ease-in);
			@include sm {
				font-size: 14px; }
			@include xs {
				position: relative;
				z-index: 3;
				padding: 0 20px;
				background-color: #22273B;
				@include f('cl', 22); }
			&:hover {
				color: $blue; } } } }

/* menu
	----------------------------------- */
.menu {
	position: relative;
	@include md {
		margin: 11px 0 0 10px; }
	@include xs {
		margin: 15px 0 0;
		width: 100%; }
	@include r(399) {
		display: none; }
	&::before {
		content: '';
		position: absolute;
		left: -250%;
		bottom: -54px;
		z-index: 1;
		width: 700%;
		height: 1px;
		background-color: rgba(#fff, .1);
		pointer-events: none;
		@include sm {
			bottom: -39px; }
		@include xs {
			bottom: -19px; } }
	&__list {
		font-size: 0;
		@include xs {
			display: flex;
			justify-content: space-between; }
		&-item {
			position: relative;
			padding: 0 12px;
			display: inline-block;
			vertical-align: top;
			@include sm {
				padding: 0 9px; }
			@include xs {
				padding: 0; } }
		&-link {
			position: relative;
			display: block;
			font-size: 18px;
			color: $white;
			@include sm {
				font-size: 15px; } } }
	&__line {
		position: absolute;
		bottom: -53px;
		z-index: 1;
		height: 5px;
		transition: transform .25s linear, width .25s ease-in, opacity .4s;
		background-color: $blue;
		opacity: 0;
		pointer-events: none;
		@include sm {
			bottom: -38px; }
		@include xs {
			bottom: -19px; }
		&.is-init {
			opacity: 1; } } }

/* call-me
	----------------------------------- */
.call-me {
	position: relative;
	padding-left: 75px;
	color: $white;
	&::before {
		position: absolute;
		top: 3px;
		left: 3px;
		@include s('phone');
		content: '';
		@include md {
			display: none; } }
	&__number {
		display: block;
		font-size: 20px;
		line-height: 1;
		@include lg {
			font-size: 18px; }
		@include sm {
			font-size: 16px; } }
	&__btn {
		font-size: 15px;
		color: inherit;
		background-color: transparent;
		span {
			margin-top: 5px;
			padding-bottom: 1px;
			background-image: linear-gradient(to right, rgba(#fff, .4) 50%, transparent 50%);
			background-position: 100% 100%;
			background-size: 7px 1px; }
		&:hover {
			background-image: none;
			color: $white; } } }
