.footer {
	padding: 60px 0 40px;
	color: #6f6f6f;
	@include lg {
		padding: 35px 0; }
	@include md {
		position: relative;
		padding: 25px 0 35px; }
	@include xs {
		padding: 20px 0 35px; }
	&__container {
		font-size: 0;
		@include justify;
		@include lg {
			max-width: 1286px; }
		@include xs {
			display: flex;
			flex-direction: column;
			align-items: center; } }
	&__col {
		display: inline-block;
		vertical-align: middle;
		text-align: left;
		line-height: 1.2;
		&:nth-child(1) {
			width: percentage(430 / 1256);
			@include md {
				width: 33%; }
			@include xs {
				width: 100%;
				order: 3; } }
		&:nth-child(2) {
			width: 15%;
			@include md {
				width: 26%; }
			@include xs {
				width: 100%; }
			@include xs; }
		&:nth-child(3) {
			width: 33.2%;
			@include md {
				width: 30%; }
			@include xs {
				margin: 15px 0;
				width: 100%; } } }
	&__copy {
		font-size: 16px;
		text-align-last: left;
		@include md {
			max-width: 260px; }
		@include xs {
			max-width: none;
			font-size: 15px;
			text-align-last: center; } }
	&__socials {
		text-align-last: center; }
	&__contact {
		@include md {
			text-align: right;
			text-align-last: right; }
		@include xs {
			text-align-last: center; } }
	&__contact-btn {
		display: inline-block;
		vertical-align: middle;
		padding: 15px 32px;
		@include md {
			padding: 10px 17px; } }
	&__contact-dev {
		display: inline-block;
		vertical-align: middle;
		margin-left: 50px;
		font-size: 16px;
		@include md {
			display: block;
			position: absolute;
			right: 10px;
			bottom: 7px;
			margin-left: 0; }
		@include xs {
			bottom: 10px;
			left: 50%;
			transform: translateX(-50%);
			white-space: nowrap; }
		a {
			color: #2d2d2d;
			text-decoration: underline;
			&:hover {
				text-decoration: none; } } } }

/* socials
	----------------------------------- */
.socials {
	font-size: 0;
	&__link {
		display: inline-block;
		vertical-align: middle;
		font-size: 16px;
		& + & {
			margin-left: 21px; }
		svg {
			display: inline-block;
			vertical-align: middle;
			fill: #959595;
			@include trs;
			&:hover {
				fill: $blue; } }
		.icon-tw {
			font-size: 17px; }
		.icon-gp {
			font-size: 18px; } } }
