.hamburger.is-active .hamburger__line:nth-child(1), .hamburger.is-active .hamburger__line:nth-child(2), .hamburger.is-active .hamburger__line:nth-child(3) { -webkit-transition-delay: 0.2s; -o-transition-delay: 0.2s; transition-delay: 0.2s; }

