.restaurant {
	background-color: #f4f4f4;
	&_default {
		padding: 32px 0 43px;
		@include lg {
			padding: 20px 0 25px; }
		@include xs {
			padding: 20px 0 10px; } }
	&__row {
		padding: 35px 0;
		@include clr;
		@include lg {
			padding: 50px 0 25px; }
		@include xs {
			padding: 40px 0 0; } }
	&__person {
		float: left;
		width: 50%;
		white-space: nowrap;
		@include sm {
			width: 48%;
			padding: 0 10px; }
		@include r(599) {
			width: 100%;
			margin-bottom: 20px; }
		&-pic-wrap {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: percentage(246 / 628);
			@include md {
				width: 35%; }
			@include sm {
				width: 53%;
				display: block;
				margin: 0 auto; }
			@include r(599) {
				width: 50%; }
			&::after {
				position: absolute;
				top: -5%;
				right: -42%;
				width: 58.1%;
				height: 43.9%;
				z-index: 1;
				content: '';
				background: url(../img/bg/img-decor.png) no-repeat 50% 50%/cover; } }
		&-pic {
			position: relative;
			z-index: 2;
			overflow: hidden;
			border: 6px solid $white;
			border-radius: 50%;
			background: $white; }
		&-img {
			width: 100%;
			height: 100%; }
		&-text {
			display: inline-block;
			vertical-align: sub;
			width: calc(100% - 270px);
			padding-left: 30px;
			@include lg {
				width: calc(100% - 240px); }
			@include md {
				width: calc(100% - 180px); }
			@include sm {
				margin-top: 10px;
				width: 100%;
				text-align: center;
				padding-left: 0; } }
		&-name {
			@include f('cb', 20);
			@include md {
				font-size: 18px; } }
		&-pos {
			font-size: 16px; } }

	&__person:nth-child(2n) &__person-text {
		padding-left: 45px;
		@include sm {
			padding-left: 0; } } }
