.about {
	&__container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		min-height: 530px;
		@include lg {
			min-height: 400px; }
		@include md {
			min-height: 300px; }
		@include xs {
			min-height: 0;
			padding: 25px 10px; } }
	&__title {
		margin-bottom: 35px;
		font-size: 40px;
		line-height: 1.2;
		@include lg {
			margin-bottom: 20px;
			font-size: 37px; }
		@include md {
			font-size: 30px; }
		@include sm {
			font-size: 25px; } }
	&__descr {
		line-height: (26/18);
		color: #6f6f6f; }
	&__map-wrap {
		position: relative; }
	&__map {
		height: 600px;
		@include sm {
			height: 400px; }
		@include xs {
			display: none; } }
	&__address {
		position: absolute;
		top: 150px;
		left: 50%;
		margin-left: -33%;
		z-index: 2;
		padding: 50px 61px 47px;
		background: $white;
		font-style: normal;
		@include lg {
			margin-left: -48%; }
		@include md {
			padding: 30px 40px 35px; }
		@include sm {
			top: 40px;
			padding: 20px 20px 25px; }
		@include xs {
			position: static;
			margin: 0;
			background: #F4F4F4; }
		&-title {
			margin-bottom: 40px;
			font-size: 26px;
			text-align: center;
			@include sm {
				margin-bottom: 20px;
				font-size: 24px; } }
		&-list {
			padding-left: 32px; }
		&-str {
			position: relative;
			color: #6f6f6f;
			line-height: 1.3;
			&:not(:last-child) {
				margin-bottom: 15px; }
			&::before {
				position: absolute;
				left: -26px;
				top: 12px;
				z-index: 1;
				margin-right: 5px;
				content: '';
				width: 15px;
				height: 1px;
				background-color: #dcdcdc; } } } }
